<template>
  <div>
    <div class="wrap">
      <div class="search">
        <input
          type="text"
          class="searchTerm"
          placeholder="Where would you like to travel?"
          v-model="origin"
        />
        <input
          type="date"
          class="searchTerm"
          placeholder="When would you like to travel?"
          v-model="date"
        />
        <button type="submit" class="searchButton" @click="search()">
          Search
        </button>
      </div>
    </div>
    <div class="loading" v-if="loading">
      <div class="preload">
        <img
          class="airplane"
          src="data:image/png;base64,"
        />
        <h3>Loading awesome trips...</h3>
        <img
          class="cloud1"
          src="data:image/png;base64,"
        />
        <img
          class="cloud2"
          src="data:image/png;base64,"
        />
        <img
          class="cloud3"
          src="data:image/png;base64,"
        />
      </div>
    </div>
    <div class="card-container" v-else>
      <BookingCard
        v-for="(item, index) in items"
        :key="index"
        :id="calcId(index)"
        :inBookings="false"
        @clickBook="book(item)"
      >
        <template #image>
          <img :src="src()" />
        </template>
        <template #destination>
          {{
            formatAirport(item.destination) + " - " + item.destination
          }}</template
        >
        <template #departureDate
          >Departure date: {{ formatDate(item.departureDate) }}
        </template>
        <template #returnDate
          >Return date: {{ formatDate(item.returnDate) }}</template
        >
        <template #price> {{ item.price }} EUR</template>
      </BookingCard>
    </div>
    <LoginModal
      v-if="showModal"
      @close="showModal = false"
      :booking="booking"
    />
  </div>
</template>

<script>
import { store } from "@/store";
import router from "@/routers";
import BookingCard from "./BookingCard.vue";
import LoginModal from "./LoginModal.vue";
import { getFlights } from "../services/FlightService";
import { getAirportName } from "../utils/AirportCodes";
import { createFlightBooking } from "../services/MyBookingsService";

export default {
  name: "DashboardView",
  components: {
    BookingCard,
    LoginModal,
  },
  props: {
    msg: String,
  },
  data() {
    return {
      showModal: false,
      loading: true,
      items: [
        {
          id: 1,
          origin: "MAD",
          destination: "LON",
          departureDate: [2023, 2, 10],
          returnDate: [2023, 2, 13],
          price: 128.5,
        },
        {
          id: 2,
          origin: "MAD",
          destination: "NYC",
          departureDate: [2023, 2, 18],
          returnDate: [2023, 2, 22],
          price: 750.25,
        },
        {
          id: 3,
          origin: "MAD",
          destination: "BOS",
          departureDate: [2023, 3, 6],
          returnDate: [2023, 2, 22],
          price: 326.0,
        },
      ],
    };
  },

  methods: {
    calcId(index) {
      return `id-${index}`;
    },

    async book(item) {
      console.log(item, store.state.user.email);
      if (store.state.user.email) {
        await createFlightBooking(
          item.origin,
          item.destination,
          item.departureDate,
          item.returnDate,
          item.price
        );
        router.push({ name: "MyBookings" });
      } else {
        this.booking = item;
        this.showModal = true;
      }
    },

    src() {
      return `https://picsum.photos/id/${
        this.items.length + Math.floor(Math.random() * (180 - 30)) + 30
      }/500/300`;
    },

    formatDate(date) {
      return date.toString().split(",").reverse().join("-");
    },

    formatAirport(iataCode) {
      return getAirportName(iataCode);
    },

    search() {
      this.getSearchResults(this.origin, this.date);
      this.loading = false;
    },

    async getSearchResults(origin, departureDate) {
      await getFlights(origin, departureDate);
      this.loading = false;
      /*
      if (origin && departureDate) {
        const response = await getFlights(origin, departureDate);
        this.items = response;
      } else if (departureDate) {
        const response = await getFlightsByDate(departureDate);
        this.items = response;
      } else if (origin) {
        const response = await getFlightsByOrigin(origin);
        this.items = response;
      }*/
    },
  },
  created() {
    this.getSearchResults("LON", "2023-10-23");
  },
};
</script>

