<template>
  <div>
    <div class="wrap">
      <div class="search">
        <input
          type="text"
          class="searchTerm"
          placeholder="Where would you like to travel?"
          v-model="destination"
        />
        <input
          type="date"
          class="searchTerm"
          placeholder="When would you arrive?"
          v-model="checkInDate"
        />
        <input
          type="date"
          class="searchTerm"
          placeholder="Util when?"
          v-model="checkOutDate"
        />
        <input
          type="number"
          class="searchTerm"
          placeholder="How many guests?"
          v-model="guests"
        />
        <button type="submit" class="searchButton" @click="search()">
          Search
        </button>
      </div>
    </div>
    <div class="loading" v-if="loading">
      <div class="container">
        <h1>Loading cosy places ...</h1>

        <div class="house" />
      </div>
    </div>
    <div class="card-container" v-else>
      <BookingCard
        v-for="(item, index) in items"
        :key="index"
        :id="calcId(index)"
        :inBookings="false"
        @clickBook="book(item)"
      >
        <template #image>
          <img :src="src()" />
        </template>
        <template #destination>Hotel name: {{ item.name }}</template>
        <template #departureDate
          >Checkin date: {{ formatDate(item.checkInDate) }}
        </template>
        <template #returnDate
          >Checkout date: {{ formatDate(item.checkOutDate) }}</template
        >
        <template #guests>Guests: {{ item.guests }}</template>

        <template #price> {{ item.price }} EUR</template>
      </BookingCard>
    </div>
    <LoginModal
      v-if="showModal"
      @close="showModal = false"
      :booking="booking"
    />
  </div>
</template>

<script>
import { store } from "@/store";
import router from "@/routers";
import BookingCard from "./BookingCard.vue";
import LoginModal from "./LoginModal.vue";
import { getFlights } from "../services/FlightService";
import { createHotelBooking } from "../services/MyBookingsService";

export default {
  name: "HotelsView",
  components: {
    BookingCard,
    LoginModal,
  },
  props: {
    msg: String,
  },
  data() {
    return {
      showModal: false,
      items: [
        {
          name: "Hilton",
          checkInDate: [2023, 2, 10],
          checkOutDate: [2023, 2, 13],
          guests: 1,
          price: 128.5,
        },
        {
          name: "4 Seasons",
          checkInDate: [2023, 2, 18],
          returnDate: [2023, 2, 22],
          guests: 1,
          price: 750.25,
        },
        {
          name: "Villa",
          checkInDate: [2023, 3, 6],
          checkOutDate: [2023, 2, 22],
          guests: 1,
          price: 326.0,
        },
      ],

      booking: null,
      loading: true,
    };
  },

  methods: {
    calcId(index) {
      return `id-${index}`;
    },

    async book(item) {
      if (store.state.user.email) {
        await createHotelBooking(
          item.name,
          item.checkInDate,
          item.checkOutDate,
          item.guests,
          item.price
        );
        router.push({ name: "MyBookings" });
      } else {
        this.booking = item;
        this.showModal = true;
      }
    },

    src() {
      return `https://picsum.photos/id/${
        this.items.length + Math.floor(Math.random() * (180 - 30)) + 30
      }/500/300`;
    },

    formatDate(date) {
      return date.toString().split(",").reverse().join("-");
    },

    search() {
      this.getSearchResults(
        this.destination,
        this.checkInDate,
        this.checkOutDate,
        this.guests
      );
      this.loading = false;
    },
    /*
    async getSearchResults(destination, checkInDate, checkOutDate, guests) {
      await getHotels(destination, checkInDate, checkOutDate, guests);
      // this.items = response;
    },*/

    async getSearchResults(origin, departureDate) {
      await getFlights(origin, departureDate);
      this.loading = false;
    },
  },
  created() {
    this.getSearchResults("LON", "2023-10-23");
  },
};
</script>

